import React, {useState} from 'react';

import style from '../../assets/style/scss/busket/busket.module.css'
import BusketItem from "./busket-item/busket-item";
import {NavLink} from "react-router-dom";

const Busket = (props) => {

    let items = props.data.map(i => <BusketItem data={i}/>)
    const sum = props.data.reduce(function (sum, {all_price}){
        return sum + all_price
    },0)
    return (
        <div className={style.main}>
            <h5> </h5>
            <div className={style.main__general}>
            <div className={style.main__navbar}>
                <div className={style.main__first}><p></p></div>
                <div className={style.main__second}><p> </p></div>
                <div className={style.main__third}><p></p></div>
                <div className={style.main__four}><p></p></div>
                <div className={style.main__five}><p></p></div>
            </div>
            {items}

            <div className={style.all}>
                <div className={style.all__item}><p>: </p></div>
                <div className={style.all__item}><p>{sum} .</p></div>
            </div>

                <div className={style.oforml}><NavLink to={'/delivery_information'}> </NavLink></div>

            </div>
        </div>
    );
};

export default Busket;